body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-gilroy-regular: Gilroy-Regular;
  --font-gilroy-light: Gilroy-Light;
  --font-gilroy-bold: Gilroy-Bold;
  --font-gilroy-medium: Gilroy-Medium;
  --font-gilroy-black: Gilroy-Black;
  --font-inter: Inter;

  /* font sizes */
  --font-size-5xl: 1.5rem;
  --font-size-lg: 1.13rem;
  --font-size-base: 1rem;

  /* Colors */
  --color-darkslategray-100: #312d2e;
  --color-lightgray: #d2d2d2;
  --color-white: #fff;
  --color-gray-100: #2b2526;

  /* Gaps */
  --gap-base: 1rem;

  /* Paddings */
  --padding-13xl: 2rem;

  /* border radiuses */
  --br-23xl: 42px;
  --br-7xl: 26px;
}
